import React from 'react';

import '../css/Socials.css'
import Svg from 'react-inlinesvg'

function Socials() {

    return (
        <div className='Socials' >
            <div className='community'>
                <div className='socialMedia'>
                    <a target='_blank' rel="noopener noreferrer" href='https://discord.gg'><Svg src='/img/discord.svg' /></a>
                </div>
                <div className='socialMedia'>
                    <a target='_blank' rel="noopener noreferrer" href='https://twitter.com/solasphereNFT'><Svg src='/img/twitter.svg' /></a>
                </div>
            </div>
        </div >
    );
}

export default Socials;
