import React, { useState } from 'react';


import { LAMPORTS_PER_SOL, } from '@solana/web3.js'
import { shortenAddress } from '../utils/shortenAddress';
import Svg from 'react-inlinesvg'

import '../css/ActiveGames.css'
import '../css/Coin.css'
import '../css/Modal.css'
import Game from '../interfaces/Game';
import { createAvatar } from '@dicebear/avatars'
import * as avatarStyle from '@dicebear/adventurer';
import { Link } from 'react-router-dom';
import moment from 'moment'
import '../css/RecentGames.css'
import FairnessModal from './FairnessModal';
import { displayName } from '../utils/displayName';

interface Props {
    games: Game[]
}

function RecentGames(props: Props) {
    const [gameForFairness, setGameForFairness] = useState<Game | null>()

    const closeFairnessModal = () => {
        setGameForFairness(null)
    }

    return (
        <div className='recentGamesWrapper'>
            <table className='recentGames'>
                <thead>
                    <tr>
                        <th className='date'>Date</th>
                        <th>Creator</th>
                        <th>Opponent</th>
                        <th>Bet</th>
                        <th>Result</th>
                        <th>Winner</th>
                        <th>Provably Fair</th>
                    </tr>
                </thead>
                <tbody>
                    {props.games.map(game =>
                        game.opponent && game.winner && game.result !== null ?
                            <tr key={game._id} className='recentGame'>
                                <td className='date'><div className="tdContent">{moment(game.updatedAt).format('MMM DD, h:mm A')}</div></td>
                                <td><div className="user tdContent"> <Link to={`/u/${game.creator.publicKey}`}> <Svg src={createAvatar(avatarStyle, { seed: game.creator.publicKey })} /> <p className='publicKey'>{displayName(game.creator)}</p></Link></div></td>
                                <td><div className="user tdContent"><Link to={`/u/${game.opponent.publicKey}`}><Svg src={createAvatar(avatarStyle, { seed: game.opponent.publicKey })} />  <p className='publicKey'>{displayName(game.opponent)}</p></Link></div></td>
                                <td><div className="bet tdContent">{game.amount / LAMPORTS_PER_SOL} <Svg src='./img/coins.svg' /></div></td>
                                <td><div className="coin tdContent"><img src={`./img/coin${game.result}.png`} /></div></td>
                                <td><div className='user tdContent'> <Link to={`/u/${game.winner.publicKey}`}> <Svg src={createAvatar(avatarStyle, { seed: game.winner.publicKey })} /> <p className='publicKey'>{displayName(game.winner)}</p></Link></div></td>
                                <td><button onClick={() => setGameForFairness(game)} className='openFairnessB'>Fairness</button></td>
                            </tr> : null)}
                </tbody>
            </table>
            {gameForFairness ? <FairnessModal game={gameForFairness} closeFairnessModal={closeFairnessModal} /> : null}
        </div>
    );
}

export default RecentGames;